.event-card {
  margin: 1rem 0;
  padding: 1.2rem;
  border-radius: .6rem;
  cursor: pointer;
  color: #1b4332;
  background-color: #fff;
  box-shadow: 2px 2px 1rem rgba(0, 0, 0, 0.3);

  display: flex;
}

.event-card .avatar {
  height: 6rem;
  width: 6rem;
  margin-right: 2rem;
  border-radius: 50%;
}

.event-card .event-card-info {
  width: 100%;
}

.event-card .feedback-tray {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.event-card .feedback-container {
  display: flex;
  align-items: center;
}

.event-card .likes {
  margin-right: 1rem;
  /* display: flex;
  align-items: center; */
}

.event-card .likes-heart {
  font-size: 2rem;
}

.event-card .likes-heart.does-like {
  color: red;
}

.event-card .likes-heart.no-like {
  color: #000;
}

.attendee-list {
  display: flex;
  margin-left: 2rem;
}

.attendee-avatar {
  height: 4rem;
  width: 4rem;
  border: 3px solid #fff;
  border-radius: 50%;
  margin-left: -.8rem;
}

.delete-btn {
  padding: .7rem 1.4rem;
  background-color: var(--red-light);
  color: var(--red-dark);
  margin-right: .6rem;
  border: none;
  border-radius: 3rem;
  transition: all 400ms;
}

.delete-btn:hover {
  color: #fff;
  /* background-color: #da4328; */
  background-color: var(--red-medium);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
}

.attend-btn {
  padding: .7rem 1.4rem;
  background-color: #7eb497;
  /* background-color: #cfd8d7; */
  color: #1b4332;
  border: none;
  /* justify-self: flex-end; */
  border-radius: 3rem;
  transition: all 400ms;
}

.attend-btn:hover {
  color: #fff;
  /* background-color: #3f815d; */
  background-color: #3f815d;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
}
